<template>
  <div class="custom-select" @blur="open = false">
    <div
        class="selected"
        :class="{ open: open }"
        @click="open = !open"
    >
      {{ itemTitle ? selected[itemTitle] : selected }}
    </div>
    <div class="items" :class="{ selectHide: !open }">
      <div
          v-for="(option, index) in options"
          :key="`${index}-v-select-list-item`"
          @click="
            selected = option;
            open = false;
            $emit('input', option);
          "
      >
        {{ itemTitle ? option[itemTitle] : option }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    options: {
      type: Array,
      required: true,
    },
    default: {
      type: Number,
      required: false,
      default: null,
    },
    itemTitle: {
      type: String,
      default: null
    },
    itemValue: {
      type: String,
      default: null
    },
    returnObject: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      selected: this.default
          ? this.options[this.default]
          : this.options.length > 0
              ? this.options[0]
              : null,
      open: false,
    };
  },
  mounted() {
    this.$emit("input", this.selected);
  },
};
</script>

<style scoped>
.custom-select {
  position: relative;
  width: 100%;
  text-align: left;
  outline: none;
  height: 47px;
  line-height: 47px;
}

.custom-select .selected {
  background-color: white;
  border-radius: 6px;
  border: 1px solid #999;
  color: #999999;
  padding-left: 1em;
  cursor: pointer;
  user-select: none;
}

.custom-select .selected.open {
  border-radius: 6px 6px 0px 0px;
}

.custom-select .selected:after {
  position: absolute;
  content: "";
  top: 22px;
  right: 1em;
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-color: #999 transparent transparent transparent;
}

.custom-select .items {
  color: #fff;
  border-radius: 0px 0px 6px 6px;
  overflow: hidden;
  border-right: 1px solid #ad8225;
  border-left: 1px solid #ad8225;
  border-bottom: 1px solid #ad8225;
  position: absolute;
  background-color: #999;
  left: 0;
  right: 0;
  z-index: 1;
}

.custom-select .items div {
  color: #fff;
  padding-left: 1em;
  cursor: pointer;
  user-select: none;
}

.custom-select .items div:hover {
  background-color: #ad8225;
}

.selectHide {
  display: none;
}
</style>